        button {
            display: block;
            float: right;
        }
        .half-stat-block {
            display: block;
            padding: 5px;
            width: 55%;
        }
    }
    .stat-label {
        display: block;
    }
    input {
        height: 1.75em;
        width: 50% ;
    }
    button {
        height: 1.75em;
        padding: 2px;
    }
    .random-all {
        margin: auto;
        padding: 5px;
    }
    .half-stat-block {
        width: 50% ;
